import PressIconPlus from '../../../../packages/press-icon-plus/press-icon-plus.vue';

export default {
  components: {
    PressIconPlus,
  },
  props: {
  },
  data() {
    return {

    };
  },
  computed: {
    showBack() {
      const res = this.$route.meta.showBack;
      if (res === undefined) return true;
      return res;
    },
    title() {
      return this.$route.meta.title;
    },
    hideNavigator() {
      return this.$route.meta.hideNavigator || false;
    },
  },
  mounted() {
    console.log('[route]', this.$route);
  },
  methods: {
    onBack() {
      if (!this.showBack) return;
      this.$router.back();
    },
  },
};
